<template>
  <div class="inline-block">
    <div v-if="step === 1">
      <h3>添加成员</h3>
      <UserInput @savePerson="savePerson"></UserInput>
    </div>

    <div v-else-if="step === 2">
      <h3 class="text-center">此成员是否出资?</h3>
      <div class="flex-row align-center">
        <button type="button" @click="step = 3">是</button>
        <div class="sp15"></div>
        <button type="button" @click="addPerson">否</button>
      </div>
    </div>

    <PersonExpense v-else-if="step === 3"
      :person="person" 
      @confirmExpense="addPersonWithExpense" 
      ref="personExpense"></PersonExpense>

  </div>
</template>

<script>
/* eslint-disable */
import UserInput from './UserInput.vue'
import PersonExpense from './PersonExpense.vue'

export default {
  components: {
    UserInput,
    PersonExpense
  },

  data() {
    return {
      person: {},
      step: 1
    }
  },

  computed: {
    savedUsers() {
      return this.$store.state.users
    }
  },

  methods: {
    savePerson(payload) {
      this.person = Object.assign({}, this.person, payload)
      this.step = 2
    },

    addPersonWithExpense(expense) {
      this.$set(this.person, 'expenses', expense)
      this.$store.commit('addUser', this.person)
      this.$emit('getCurrentResult', true)
      this.reset()
    },

    addPerson() {
      this.$store.commit('addUser', this.person)
      // this.$emit('addMorePerson', true)
      this.$emit('getCurrentResult', true)
      this.reset()
    },

    reset() {
      this.person = {}
      this.step = 1
    }
  }
}
</script>
