import Vue from 'vue'
import App from './App.vue'
import store from './store'



Vue.config.productionTip = false

// Add 3rd party tool
const htmlToImage = require('html-to-image')
Vue.prototype.$htmlToImage = htmlToImage

Vue.filter('Dollarize', value => {
  let costArray = value.toString().split('').reverse(),
      newCostArray = [],
      dollarSign = '$ ',
      negative = false

  if (costArray[costArray.length - 1] === '-') {
    costArray.pop()
    negative = true
  }

  for(let i = 0; i < costArray.length; i++) {
    if ((i + 1) % 3 == 0) {
      newCostArray.push(costArray[i])

      if (i !== costArray.length - 1) {
        newCostArray.push(',')
      }
    } else {
      newCostArray.push(costArray[i])
    }
  }

  return negative ? 
          `(${dollarSign + newCostArray.reverse().join('')})` :
          dollarSign + newCostArray.reverse().join('')
})

new Vue({
  store,
  render: h => h(App)
}).$mount('#app')
