import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    users: []
  },
  getters: {
    usersWithExpense(state) {
      let arr = []
      state.users.forEach(user => {
        let newRecord = {name: user.name, uid: user.uid}
        newRecord.totalExpenses = user.expenses ? user.expenses.reduce((acc, curr) => acc + curr.cost, 0) : 0
        arr.push(newRecord)
      })
      return arr
    },
    usersWithExpenseSorted(state, getters) {
      return getters.usersWithExpense.sort((a, b) => {
        return a.totalExpenses - b.totalExpenses
      })
    },
    totalExpenses(state) {
      let total = 0
      state.users.forEach(user => {
        total += user.expenses ? user.expenses.reduce((acc, curr) => acc + curr.cost, 0) : 0
      })
      return total
    },
    averageExpense(state, getters) {
      return getters.totalExpenses ? getters.totalExpenses / state.users.length : 0
    }
  },
  mutations: {
    addUser(state, user) {
      if (this.commit('userLookup', user)) return
      state.users.push(user)
    },
    deleteUser(state, user) {
      let uid = user.uid
      state.users.forEach((user, index) => {
        if (user.uid ==- uid) {
          state.users.splice(index, 1)
        }
      })
    },
    userLookup(state, user) {
      state.users.forEach(savedUser => {
        if (savedUser.uid === user.uid) {
          return savedUser
        }
      })
    },
    reset(state) {
      state.users = []
    }
  },
  actions: {

  }
})
