<template>
  <div class="item-input-wrapper">
    <div class="item-input">
      <label for="item">
        <input type="text" name="item" id="item" placeholder="项目" v-model="item" />
      </label>
    </div>
    <div class="item-input">
      <label for="cost">
        <input type="text" name="cost" id="cost" placeholder="花费" v-model="costString" />
      </label>
    </div>
    <button type="button" @click="submit">添加</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      item: '',
      costString: ''
    }
  },
  computed: {
    cost() {
      return this.costString ? parseInt(this.costString) : 0
    }
  },
  methods: {
    submit() {
      this.$emit('saveEntry', {
        item: this.item,
        cost: this.cost
      })
      this.clearInput()
    },
    clearInput() {
      this.item = '',
      this.costString = ''
    }
  }
}
</script>


