<template>
  <div class="table-wrapper">
    <div class="table" v-if="entries.length">
      <div class="table-entry label">
        <div v-for="(label, index) in labels" :key="index">{{ label }}</div>
      </div>
      <div class="table-entry entry-item" v-for="(entry,index) in entries" :key="index">
        <div>{{ entry.item }}</div>
        <div>{{ entry.cost | Dollarize }}</div>
        <div @click="clickEntry(index)">x</div>
      </div>
      <div class="table-entry summary">
        <div>{{ sum || '小计' }}</div>
        <div>{{ total | Dollarize }}</div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  props: {
    entries: Array,
    labels: Array,
    sum: String
  },

  computed: {
    total() {
      return this.entries.reduce((acc, curr) => acc + curr.cost, 0)
    }
  },

  methods: {
    clickEntry(index) {
      this.$emit('clickEntry', index)
    }
  }
}
</script>

