  width: 300px;
  border: 1px solid rgba(0,0,0,0.1);
  border-radius: 15px;
}

.table-entry{
  position: relative;
  display: flex;
  justify-content: flex-start;
  padding: 2px 8px;

  &.entry-item {
    transition: all 0.2s;
    border-radius: 3px;

    &:hover {
      background: #ffffff;
      box-shadow: 0 1px 8px -1px rgba(0,0,0,0.1);
      cursor: pointer;

      > div:last-of-type {
        display: block;
        pointer-events: all;
        opacity: 1;
      }
    }

    > div:last-of-type {
      position: absolute;
      top: 0;
      right: 0;
      opacity: 0;
      text-align: right;
      pointer-events: none;
      transition: all 0.2s;
    }
  }

  > div {
    min-width: 150px;
    text-align: left;
