<template>  
  <div class="item-input-wrapper">
    <div class="item-input">
      <label for="name">
        <input type="text" name="name" id="name" placeholder="姓名" v-model="name" />
      </label>
    </div>

    <button type="button" @click="submit">确认</button>
  </div>
</template>

<script>

export default {
  data() {
    return {
      name: '',
    }
  },

  methods: {
    cleanInput() {
      this.name = ''
    },

    submit() {
      this.$emit('savePerson', {
        uid: '_' + Math.random().toString(36).substr(2, 9),
        name: this.name.trim(),
      })
      this.cleanInput()
    }
  }
}
</script>
