<template>
  <div>
    <h3 class="text-center">当前成员: {{ person.name }}</h3>
    <Table :entries="expenses" :labels="['项目', '花费']" v-show="expenses.length"></Table>
    
    <div v-show="!submitted">
      <div class="mb15" v-show="!expenseAdded">
        <h5>添加花费</h5>
        <ItemInput @saveEntry="addExpense"></ItemInput>
      </div>

      <div class="pt15" v-show="expenseAdded">
        <h5 class="text-center">完成并提交</h5>
        <div class="flex-row align-center">
          <button type="button" @click="attachExpenseToPerson">完成</button>
          <div class="sp15"></div>
          <button type="button" @click="expenseAdded = false">继续添加</button>
        </div>
      </div>
    </div>

    <div class="pt15" v-show="submitted">
      <h5 class="text-center">确认提交</h5>
      <div class="flex-row align-center">
        <button type="button"  @click="confirmExpense">确认</button>
        <div class="sp15"></div>
        <button type="button" @click="dettachPersonExpense">返回</button>
      </div>
    </div>

  </div>
</template>

<script>
/* eslint-disable */
import ItemInput from './ItemInput.vue'
import Table from './Table.vue'

export default {
  components: {
    Table,
    ItemInput
  },
  props: {
    person: Object,
  },
  data() {
    return {
      expenses: [],
      submitted: false,
      expenseAdded: false
    }
  },
  methods: {
    savePersonExpenseEntries() {
      if (this.person.expenses) {
        this.person.expenses = null
        this.$set(this.person, 'expenses', this.expenses)
        return
      }
      this.$set(this.person, 'expenses', this.expenses)
    },

    addExpense(payload) {
      let entry = {
        item: payload.item,
        cost: payload.cost
      }

      this.expenses.push(entry)
      this.expenseAdded = true
    },

    // deleteEntry(index) {
    //   this.expenses.splice(index, 1)
    // },

    confirmExpense() {
      this.$emit('confirmExpense', this.expenses)
      this.reset()
    },

    attachExpenseToPerson() {
      this.savePersonExpenseEntries()
      this.submitted = true
      this.expenseAdded = false
    },
    dettachPersonExpense () {
      this.person.expenses = null
      this.submitted = false
    },

    reset() {
      this.expenses = []
      this.submitted = false
    }
  }
}
</script>

