<template>
  <div class="inline-block">
    <div class="p15 bg-white" id="FinalResult">
      <h3 class="text-center">{{ boss.name }}的收账单</h3>
      <Table :labels="['成员', '应付']" :entries="result" sum="总计"></Table>
    </div>

    <div class="flex-row align-center pt15">
      <button type="button" @click="generateImage">保存结果</button>
      <div class="sp15"></div>
      <button type="button" @click="restartSession">重新开始</button>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import Table from './Table.vue'


export default {
  components: {
    Table
  },
  data() {
    return {
      result: [],
      boss: {}
    }
  },
  methods: {
    generateImage() {
      this.$htmlToImage.toJpeg(document.getElementById('FinalResult'), { quality: 0.95 })
        .then(dataUrl => {
          var link = document.createElement('a');
          link.download = `${ this.boss.name.replace(' ', '_') }的收账单.jpeg`;
          link.href = dataUrl;
          link.click();
        });
    },
    restartSession() {
      this.result = []
      this.boss = {}
      this.$store.commit('reset')
      this.$emit('restart')
    }
  },
  created() {
    let users = this.$store.getters.usersWithExpenseSorted
    if (!users) return
    let avg = this.$store.getters.averageExpense
    if (!avg) return

    this.boss = Object.assign({}, this.boss, users.pop()) 

    users.forEach(user => {
      this.result.push({
        item: user.name,
        cost: parseInt(avg) - parseInt(user.totalExpenses)
      })
    })

    console.log(this.$htmlToImage)
  }
}
</script>
