<template>
  <div class="inline-block">
    <h3 class="text-center">总花费明细</h3>
    <Table :entries="expensesList" :labels="['成员', '花费']" sum="总计"></Table>

    <div class="flex-row align-center pt15">
      <button type="button" @click="addMorePerson">添加其他成员</button>
      <div class="sp15"></div>
      <button type="button" @click="getFinalResult">开始AA</button>
    </div>
  </div>
</template>

<script>
import Table from './Table.vue'

export default {
  components: {
    Table
  },
  props: {
    users: Array
  },
  computed: {
    totalExpenses() {
      return this.$store.getters.totalExpenses
    },
    expensesList() {
      let arr = []
      this.users.forEach(user => {
        arr.push({
          item: user.name,
          cost: user.expenses ? this.getUserTotalExpense(user) : 0
        })
      })
      return arr
    }
  },
  methods: {
    getUserTotalExpense(user) {
      return user.expenses.reduce((acc, curr) => acc + curr.cost, 0)
    },
    addMorePerson() {
      this.$emit('addMorePerson', true)
    },
    getFinalResult() {
      this.$emit('getFinalResult', true)
    }
  }
}
</script>
